@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Pirata+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Pirata+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  color: #151515;
  font-family: "roboto";
}

.resume-section,
.home-page {
  text-align: center;
}
.skill-section {
  list-style: none;
  list-style-type: none;
}

.accordion-flush .accordion-button {
  display: inline-block;
}
h1 {
  font-family: "Bungee Inline";
}
h3 {
  text-decoration: underline;
  font-family: "Bungee Inline";
}

.view-resume {
  text-align: center;
  color: #151515;
  padding: 0%;
  margin: 0%;
}
.accordion-header {
  padding-top: 1rem;
}

.basics-section {
  color: #151515;
  font-family: "Bungee Inline";
}
.summary-section {
  margin-left: 8rem;
  margin-right: 8rem;
  padding-bottom: 0.5rem;
  font-family: "roboto";
}
.pure-button {
  text-decoration: none;
  font-size: 2rem;
  color: #151515;
}
.pure-button:hover {
  color: black;
}

